<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="d-lg-flex">
      <div class="chat-leftsidebar mr-lg-4">
        <div class>
          <div class="py-4 border-bottom">
            <div class="media">
              <div class="align-self-center mr-3">
                <img src="@/assets/images/users/avatar-14.jpg" class="avatar-sm rounded-circle" alt />
              </div>
              <div class="media-body">
                <h5 class="font-size-15 mt-0 mb-1">{{adminname}}</h5>
                <p v-if="msg != 'Connecting'" class="text-muted mb-0">
                  <i v-if="msg != 'Connecting'" class="mdi mdi-circle align-middle mr-1" :class="connect ? 'text-success' : 'text-danger' "></i> {{msg}}
                </p>
                <p v-else  class="text-muted mb-0">
                  <i class="mdi mdi-circle align-middle mr-1 text-warning"></i> {{msg}}
                </p>
              </div>

              <b-dropdown class="chat-noti-dropdown active" right variant="white">
                <template v-slot:button-content>
                  <i class="bx bx-bell bx-tada"></i>
                </template>
                <b-dropdown-item>Action</b-dropdown-item>
                <b-dropdown-item>Another action</b-dropdown-item>
                <b-dropdown-item>Something else here</b-dropdown-item>
              </b-dropdown>
            </div>
          </div>
        </div>

        <!-- <div class="search-box chat-search-box py-4">
          <div class="position-relative">
            <input type="text" class="form-control" placeholder="Search..." />
            <i class="bx bx-search-alt search-icon"></i>
          </div>
        </div> -->

        <div class="chat-leftsidebar-nav mt-2">
          <b-tabs pills fill content-class="my-4">
            <b-tab title="Tab 1" active>
              <template v-slot:title>
                <i class="bx bx-chat font-size-20 d-sm-none"></i>
                <span class="d-none d-sm-block">Chat</span>
              </template>
              <b-card-text>
                <div>
                  <h5 class="font-size-14 mb-3">Recent</h5>
                  <simplebar style="max-height: 410px;">
                    <ul class="list-unstyled chat-list">
                      <li
                        class
                        v-for="data of chatData"
                        :key="data.id"
                        @click="chatUsername(data.name)"
                        :class="{'active': username == data.name}"
                      >
                        <a href="javascript: void(0);">
                          <div class="media">
                            <div class="align-self-center mr-3">
                              <i :class="`mdi mdi-circle text-${data.color} font-size-10`"></i>
                            </div>
                            <div class="align-self-center mr-3" v-if="data.image">
                              <img :src="`${data.image}`" class="rounded-circle avatar-xs" alt />
                            </div>
                            <div class="avatar-xs align-self-center mr-3" v-if="!data.image">
                              <span
                                class="avatar-title rounded-circle bg-soft-primary text-primary"
                              >{{data.name.charAt(0)}}</span>
                            </div>
                            <div class="media-body overflow-hidden">
                              <h5 class="text-truncate font-size-14 mb-1">{{ data.name }}</h5>
                              <p class="text-truncate mb-0">{{ data.message }}</p>
                            </div>
                            <div class="font-size-11">{{ data.time }}</div>
                          </div>
                        </a>
                      </li>
                    </ul>
                  </simplebar>
                </div>
              </b-card-text>
            </b-tab>
            <b-tab>
              <template v-slot:title>
                <i class="bx bx-book-content font-size-20 d-sm-none"></i>
                <span class="d-none d-sm-block">Agent</span>
              </template>
              <b-card-text>
                <h5 class="font-size-14 mb-3">Contact</h5>
                <simplebar style="height: 410px;">
                  <div>
                    <div class="avatar-xs mb-3">
                      <span class="avatar-title rounded-circle bg-soft-primary text-primary">A</span>
                    </div>

                    <ul class="list-unstyled chat-list">
                      <li>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Adam Miller</h5>
                        </a>
                      </li>

                      <li>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Alfonso Fisher</h5>
                        </a>
                      </li>
                    </ul>
                  </div>

                  <div class="mt-4">
                    <div class="avatar-xs mb-3">
                      <span class="avatar-title rounded-circle bg-soft-primary text-primary">B</span>
                    </div>

                    <ul class="list-unstyled chat-list">
                      <li>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Bonnie Harney</h5>
                        </a>
                      </li>
                    </ul>
                  </div>

                  <div class="mt-4">
                    <div class="avatar-xs mb-3">
                      <span class="avatar-title rounded-circle bg-soft-primary text-primary">C</span>
                    </div>

                    <ul class="list-unstyled chat-list">
                      <li>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Charles Brown</h5>
                        </a>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Carmella Jones</h5>
                        </a>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Carrie Williams</h5>
                        </a>
                      </li>
                    </ul>
                  </div>

                  <div class="mt-4">
                    <div class="avatar-xs mb-3">
                      <span class="avatar-title rounded-circle bg-soft-primary text-primary">D</span>
                    </div>

                    <ul class="list-unstyled chat-list">
                      <li>
                        <a href="#">
                          <h5 class="font-size-14 mb-0">Dolores Minter</h5>
                        </a>
                      </li>
                    </ul>
                  </div>
                </simplebar>
              </b-card-text>
            </b-tab>
          </b-tabs>
        </div>
      </div>
      <div class="w-100 user-chat">
        <div class="card">
          <div class="p-4 border-bottom">
            <div class="row">
              <div class="col-md-4 col-9">
                <h5 class="font-size-15 mb-1">{{username}}</h5>
                <p class="text-muted mb-0">
                  <i class="mdi mdi-circle text-success align-middle mr-1"></i> {{ activity }}
                </p>
              </div>
              <!-- {{chatMessagesData}}<br> -->
              <!-- {{tempMessagesData}} -->
              <div class="col-md-8 col-3">
                <ul class="list-inline user-chat-nav text-right mb-0">
                  <li class="list-inline-item d-none d-sm-inline-block">
                    <b-dropdown
                      menu-class="dropdown-menu-md"
                      variant="white"
                      right
                      toggle-class="nav-btn"
                    >
                      <template v-slot:button-content>
                        <i class="bx bx-search-alt-2"></i>
                      </template>
                      <form class="p-3">
                        <div class="form-group m-0">
                          <div class="input-group">
                            <input
                              type="text"
                              class="form-control"
                              placeholder="Search ..."
                              aria-label="Recipient's username"
                            />
                            <div class="input-group-append">
                              <button class="btn btn-primary" type="submit">
                                <i class="mdi mdi-magnify"></i>
                              </button>
                            </div>
                          </div>
                        </div>
                      </form>
                    </b-dropdown>
                  </li>
                  <li class="list-inline-item d-none d-sm-inline-block">
                    <b-dropdown toggle-class="nav-btn" right variant="white">
                      <template v-slot:button-content>
                        <i class="bx bx-cog"></i>
                      </template>
                      <b-dropdown-item>View Profile</b-dropdown-item>
                      <b-dropdown-item>Clear chat</b-dropdown-item>
                      <b-dropdown-item>Muted</b-dropdown-item>
                      <b-dropdown-item>Delete</b-dropdown-item>
                    </b-dropdown>
                  </li>

                  <li class="list-inline-item">
                    <b-dropdown toggle-class="nav-btn" right variant="white">
                      <template v-slot:button-content>
                        <i class="bx bx-dots-horizontal-rounded"></i>
                      </template>
                      <b-dropdown-item>Action</b-dropdown-item>
                      <b-dropdown-item>Another action</b-dropdown-item>
                      <b-dropdown-item>Something else</b-dropdown-item>
                    </b-dropdown>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="chat-users">
            <div class="chat-conversation p-3">
              <simplebar ref="simplebarmsg" v-on:scroll="tss" style="max-height: 470px;">
                <div v-if="chatMessagesData == 0" class="pt-5">
                  <div class="col-12 p-5 mt-5 text-center ">
                    <div class="spinner-border text-secondary">
                      <span class="sr-only">Loading..</span>
                    </div>
                  </div>
                </div>
                <div v-else>
                  <ul class="list-unstyled" v-for="(dataMessage, date) in chatMessagesData" :key="date">
                    <li>
                      <div class="chat-day-title">
                        <span class="title">{{date}}</span>
                      </div>
                    </li>
                    
                    <li
                      v-for="msg in dataMessage"
                      :key="msg.id"
                      :class="msg.uid != '' ? 'mr-5':'right ml-5'"
                    >
                      <div class="conversation-list">
                        <b-dropdown variant="white">
                          <template v-slot:button-content>
                            <i class="bx bx-dots-vertical-rounded"></i>
                          </template>
                          <b-dropdown-item>Copy</b-dropdown-item>
                          <b-dropdown-item>Save</b-dropdown-item>
                          <b-dropdown-item>Forward</b-dropdown-item>
                          <b-dropdown-item>Delete</b-dropdown-item>
                        </b-dropdown>
                        <div class="ctext-wrap px-3">
                          <div class="text-left">
                            <span class="mr-5">{{ msg.message }}</span><br>
                          </div>
                          <div class="text-right">
                            <span class="chat-time mb-0 ">
                              {{msg.time}}
                              <i v-if="msg.status == 1" class="bx bx-time-five align-middle"></i>
                              <i v-if="msg.status == 2" class="bx bx-check align-middle"></i>
                              <i v-if="msg.status == 3" class="bx bx-check-double align-middle"></i>
                            </span>
                          </div>
                        </div>
                      </div>
                    </li>
                  </ul>
                </div>
              </simplebar>
            </div>
            <div class="p-3 chat-input-section">
              <form @submit.prevent="formSubmit" class="row">
                <div class="col">
                  <div class="position-relative">
                    <input
                      type="text"
                      v-model="form.message"
                      class="form-control chat-input"
                      placeholder="Enter Message..."
                      @keydown="onTyping(3)"
                      @keyup="onTyping(4)"
                      :class="{ 'is-invalid': submitted && $v.form.message.$error }"
                    />
                    <div v-if="submitted && $v.form.message.$error" class="invalid-feedback">
                      <span v-if="!$v.form.message.required">This value is required.</span>
                    </div>
                    <div class="chat-input-links">
                      <ul class="list-inline mb-0">
                        <li class="list-inline-item">
                          <a
                            href="javascript: void(0);"
                            v-b-tooltip.hover
                            placement="top"
                            title="Emoji"
                          >
                            <i class="mdi mdi-emoticon-happy-outline"></i>
                          </a>
                        </li>
                        <li class="list-inline-item">
                          <a
                            href="javascript: void(0);"
                            v-b-tooltip.hover
                            placement="top"
                            title="Images"
                          >
                            <i class="mdi mdi-file-image-outline"></i>
                          </a>
                        </li>
                        <li class="list-inline-item">
                          <a
                            href="javascript: void(0);"
                            v-b-tooltip.hover
                            placement="top"
                            title="Add Files"
                          >
                            <i class="mdi mdi-file-document-outline"></i>
                          </a>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
                <div class="col-auto">
                  <button type="button" @click="sendMessage()" class="btn btn-primary btn-rounded chat-send w-md">
                    <span class="d-none d-sm-inline-block mr-2">Send</span>
                    <i class="mdi mdi-send"></i>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- end row -->
  </Layout>
</template>

<script>
import MesiboService from "@/helpers/mesibo-demo"
import simplebar from "simplebar-vue";
import Layout from "../../layouts/main";
import appConfig from "@/app.config";
import PageHeader from "@/components/page-header";
import { chatData, chatMessagesData } from "./data";
import { required } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import EventBus from "@/helpers/eventBus";
import _ from 'lodash';
// import * from ''
// import MesiboService from '@/helpers/mesibo-service';
// import { Mesibo, MesiboListener } from '../../../mesibo'

/**
 * Chat Component
 */
export default {
  page: {
    title: "Chat",
    meta: [{ name: "description", content: appConfig.description }]
  },
  components: { Layout, PageHeader, simplebar },
  data() {
    return {
      connect : false,
      msg: 'Offline',


      
      api : new Mesibo(),
      mesibo : {
        appid : 'com.test.package',
        user_token : 'eb6a1185ac49aee3e24e3466b631ca6f7caf96f4ca427da48e883f3672ia85461a2248',
        user_address : '6281225143660',
      },
      chatData: chatData,
      chatMessagesData: [],
      tempMessagesData: [],
      messages: [],
      title: this.$t("chat.live-chat"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("chat.live-chat"),
          active: true,
        },
      ],
      submitted: false,
      form: {
        message: ""
      },
      activity: '',
      adminname: JSON.parse(localStorage.getItem('user')).name,
      adminava: JSON.parse(localStorage.getItem('user')).avatar,
      username: "Steven Franklin"
    };
  },
  validations: {
    form: {
      message: { required }
    }
  },
  watch: {
    tempMessagesData: {
      handler: function (after, before) {
        this.chatMessagesData = _.groupBy(after, item => item.date);    
      },
      deep: true
    }
  },
  methods: {
    tss(e){
      console.log('Height ', e.target.scrollHeight);
      console.log('Scroll ', e.target.scrollTop);
      if (e.target.scrollTop <= 5) {
        console.log('to top');
      }
    },
    onTyping(key){
      MesiboService.setActivity(key);
    },
    /**
     * Get the name of user
     */
    chatUsername(name) {
      this.username = name;
      this.usermessage = "Hello";
      this.chatMessagesData = [];
      const currentDate = new Date();

      this.chatMessagesData.push({
        name: this.username,
        message: this.usermessage,
        time: currentDate.getHours() + ":" + currentDate.getMinutes()
      });
    },

    /**
     * Char form Submit
     */
    // eslint-disable-next-line no-unused-vars
    formSubmit(e) {
      this.submitted = true;

      // stop here if form is invalid
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      } else {
        const message = this.form.message;
        const currentDate = new Date();
        this.chatMessagesData.push({
          align: "right",
          name: "Henry Wells",
          message,
          time: currentDate.getHours() + ":" + currentDate.getMinutes()
        });
      }
      this.submitted = false;
      this.form = {};
    },
    sendMessage(){
      if (this.$v.$invalid) {
        return;
      } else {
        MesiboService.sendMessage(this.form.message);
        this.form.message = '';
        this.scrollToBottom();
      }
    },
    lla(){
      EventBus.$on("onConnect", (connect) => {
        this.connect = connect.sts;
        this.msg = connect.msg;
        if (connect.sts) {
          this.tempMessagesData = MesiboService.readMessage();
          setTimeout(() => {
            this.scrollToBottom();
          }, 300);
          MesiboService.readSummary();
        }
      });
    },
    newMessage(){
      EventBus.$on("onMessage", (m) => {
        this.tempMessagesData.push(m);
        setTimeout(() => {
          this.scrollToBottom();
        }, 300);
      });
    },
    messageStatus(){
      EventBus.$on("onMessageStatus", (m) => {
        var oldMsg = this.tempMessagesData.find(i => i.id === m.id);
        if (oldMsg) {
          const idx = this.tempMessagesData.findIndex(i => i.id == m.id);
          this.tempMessagesData[idx].status = m.status;
        }else {
          this.tempMessagesData.push(m);
        }
        setTimeout(() => {
          this.scrollToBottom();
        }, 300);
      });
    },
    onActivity(){
      EventBus.$on("onActivity", (s) => {
        if (s == 3) {
          this.activity = 'typing..';
        }else{
          this.activity = '';
        }
        console.log('act => ', s);
      });
    },
    scrollToBottom(){
      const container = this.$refs.simplebarmsg;
      container.SimpleBar.getScrollElement().scrollTop = container.SimpleBar.getScrollElement().scrollHeight;
    }
  },
  created(){
    this.lla();
    this.newMessage();
    this.messageStatus();
    this.onActivity();
  },
  mounted(){
  }
};
</script>